export default {
	props: {
		tabCur: {
			type: Number,
			default: 0
		},
		tabList: {
			type: Array
		},
		border: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			cur: 0
		}
	},
	mounted() {
		this.cur = this.tabCur
	},
	methods: {
		changeTab: function(index) {
			this.cur = index
			this.$emit('changeTab',this.cur)
		}
	},
	watch: {
		tabCur(a) {
			this.cur = a
		}
	}
}
